@use "base/variables" as v;
@use "base/mixins" as m;

.technique {
  .hero-img {
    position: relative;
    h2 {
      position: absolute;
      bottom: 1rem;
      padding-left: 1rem;
      font-size: 1.5rem;
      font-family: v.$text;
      span {
        font-family: v.$title;
      }
      @include m.mobile {
        padding-left: 2.5rem;
        bottom: 3rem;
        font-size: 2rem;
        span {
          display: block;
          padding-left: 4rem;
          font-size: 2.15rem;
        }
      }
      @include m.tablet {
        font-size: 3rem;
        padding-left: 7.5vw;
        bottom: 4rem;
        span {
          font-size: 3.5rem;
        }
      }
      @include m.laptop {
        font-size: 3rem;
        padding-left: 7vw;
        bottom: 7rem;
        span {
          font-size: 3.5rem;
        }
      }
      @include m.desktop {
        font-size: 4.5rem;
        padding-left: 7vw;
        bottom: 8rem;
        span {
          font-size: 6rem;
        }
      }
      @include m.desktopXL {
        font-size: 5.5rem;
        padding-left: 8vw;
        bottom: 12rem;
        span {
          font-size: 7rem;
        }
      }
    }
    .title-ropa {
      font-family: v.$title;
      span {
        font-family: v.$text;
      }
    }
    .h2-dark {
      color: v.$primaryPinkDark;
    }
    h2:not(.h2-dark) {
      color: v.$white;
    }
    .hero-logo {
      display: none;
      position: absolute;
      bottom: 20rem;
      width: 20%;
      padding-left: 10%;
      img {
        width: 100%;
      }
      @include m.tablet {
        display: block;
      }
      @include m.laptop {
        width: 22.5%;
        padding-left: 10%;
        bottom: 28rem;
      }
      @include m.desktop {
        bottom: 35rem;
      }
      @include m.desktopXL {
        bottom: 45rem;
      }
    }
  }
  .technique-description {
    margin: 3rem auto;
    @include m.tablet {
      margin-bottom: 5rem;
    }
  }
  h3 {
    text-align: center;
    color: v.$primaryPinkDark;
    padding: 1rem 2rem 0 2rem;
    font-family: v.$text;
    @include m.tablet {
      padding-top: 3rem;
    }
  }
  h4 {
    text-align: center;
    color: v.$primaryPinkDark;
    padding: 1rem 2rem 0 2rem;
    font-family: v.$subtitle;
    @include m.tablet {
      padding-top: 3rem;
    }
  }
  p {
    text-align: center;
    margin: 3rem;
    color: v.$secondaryGray;
    @include m.tablet {
      margin: 3rem 10rem 0;
    }
    @include m.laptop {
      margin: 3rem 15rem 0;
    }
    @include m.desktopXL {
      font-size: 1.75rem;
      margin: 3rem 25rem 0;
    }
  }
  .technique-description-2 {
    h3 {
      margin-bottom: 3rem;
      font-family: v.$title;
      font-size: 1.5rem;
      @include m.mobile {
        font-size: 1.75rem;
      }
      @include m.tablet {
        margin-bottom: 6rem;
        font-size: 2.15rem;
      }
      @include m.laptop {
        font-size: 2.5rem;
      }
      @include m.desktop {
        font-size: 3rem;
      }
      @include m.desktopXL {
        font-size: 3.5rem;
      }
    }
    p {
      margin: 1rem 2rem 2rem 2rem;
    }
    margin: auto 2rem 5rem 2rem;
    @include m.tablet {
      margin-left: 5rem;
      margin-right: 5rem;
    }
    @include m.laptop {
      margin-left: 15rem;
      margin-right: 15rem;
    }
    @include m.desktopXL {
      margin-left: 20rem;
      margin-right: 20rem;
    }
    .technique-square-1,
    .technique-square-2 {
      padding-bottom: 2rem;
      position: relative;
      @include m.tablet {
        padding-bottom: 4.5rem;
      }
      @include m.desktop {
        padding-bottom: 5rem;
      }
      @include m.desktopXL {
        padding-bottom: 5.5rem;
      }
    }
    .technique-btn-1,
    .technique-btn-2 {
      position: absolute;
      width: 80%;
      margin: auto;
      height: 6rem;
      left: 10%;
      bottom: -3rem;
      display: flex;
      justify-content: center;
      align-items: center;
      a {
        border-radius: 3rem;
        padding: 0.5rem 2rem;
      }
      @include m.tablet {
        height: 7rem;
        bottom: -3.5rem;
      }
      @include m.desktop {
        height: 8rem;
        bottom: -4rem;
      }
      @include m.desktop {
        height: 9rem;
        bottom: -4.5rem;
      }
    }
    .tecnhique-bg-1,
    .tecnhique-bg-2 {
      height: 3rem;
      width: 50%;
      z-index: -1;
      position: absolute;
      top: -1rem;
      right: 1rem;
      @include m.tablet {
        height: 10rem;
        top: -3rem;
        right: 1.5rem;
      }
    }
    .technique-square-1 {
      background-color: v.$primaryPinkDark;
      padding-top: 2rem;
      h4 {
        color: v.$primaryPink;
        font-size: 1.6rem;
        font-family: v.$subtitle;
        @include m.tablet {
          font-size: 2rem;
        }
        @include m.laptop {
          font-size: 2.1rem;
        }
        @include m.desktop {
          font-size: 2.5rem;
        }
      }
      p {
        color: v.$white;
      }
      @include m.desktop {
        width: 90rem;
        margin-left: auto;
        margin-right: auto;
      }
    }
    .technique-btn-1 {
      background-color: v.$primaryBeige;
      a {
        color: v.$white;
        border: 2px solid v.$primaryPinkDark;
        background-color: v.$primaryPinkDark;
        font-family: v.$text;
        @include m.mobile {
          font-size: 1.25rem;
        }
        @include m.tablet {
          font-size: 1.5rem;
        }
        @include m.desktop {
          font-size: 1.8rem;
        }
      }
    }
    .tecnhique-bg-1 {
      background-color: v.$primaryBeige;
    }
    .technique-square-2 {
      background-color: v.$primaryBeige;
      h4 {
        color: v.$primaryPinkDark;
        font-size: 1.6rem;
        font-family: v.$subtitle;
        @include m.tablet {
          font-size: 2rem;
        }
        @include m.laptop {
          font-size: 2.1rem;
        }
        @include m.desktop {
          font-size: 2.5rem;
        }
      }
      p {
        color: v.$secondaryGray;
      }
    }
    .technique-btn-2 {
      background-color: v.$primaryPinkDark;
      a {
        color: v.$white;
        border: 2px solid v.$primaryBeige;
        background-color: v.$primaryBeige;
        color: v.$primaryPinkDark;
        font-family: v.$text;
        @include m.mobile {
          font-size: 1.25rem;
        }
        @include m.tablet {
          font-size: 1.5rem;
        }
        @include m.desktop {
          font-size: 1.8rem;
        }
      }
    }
    .tecnhique-bg-2 {
      background-color: v.$primaryPinkDark;
    }
    margin-bottom: 5rem;
    @include m.mobile {
      margin-bottom: 7rem;
    }
    @include m.tablet {
      margin-bottom: 10rem;
    }
    @include m.laptop {
      margin-bottom: 12.5rem;
    }
  }
}

.service {
  margin-top: 2rem;
  margin-bottom: 2rem;
  // margin: 5rem;
  // height: 150vw;
  @include m.tablet {
    margin-top: 8rem;
    margin-bottom: 5rem;
    // height: 15rem;
    // margin-top: 5rem;
  }
  @include m.laptop {
    // height: 20rem;
    margin-top: 15rem;
    margin-bottom: 10rem;
  }
  @include m.desktop {
    // height: 30rem;
    margin-top: 20rem;
  }
  .service-head {
    // position: relative;
    h2 {
      text-transform: uppercase;
      // position: absolute;
      // top: 1rem;
      font-family: v.$title;
      color: v.$primaryPinkDark;
      font-size: 2rem;
      text-align: center;
      width: 100%;
      @include m.tablet {
        width: 70%;
        font-size: 2.5rem;
        margin-left: 19vw;
        text-align: left;
      }
      @include m.laptop {
        width: 60%;
        font-size: 3.5rem;
        margin-left: 21vw;
      }
      @include m.desktop {
        width: 60%;
        margin-left: 19vw;
        font-size: 5rem;
      }
      @include m.desktopXL {
        // margin-left: 20%;
        // font-size: 5rem;
        margin-left: 22vw;
      }
      .head-1 {
        position: relative;
        .head-2 {
          position: absolute;
          color: v.$white;
          width: 200%;
          left: 100%;
          transform: translate(-75%, 2.5rem);
          @include m.tablet {
            left: 0;
            transform: unset;
            width: 70rem;
            top: 3.5rem;
          }
          @include m.desktop {
            top: 5.5rem;
          }
        }
      }
    }
    .bottom-area {
      color: v.$white;
      z-index: -1;
      height: auto;
      // width: -webkit-fill-available;
      // margin-left: 16vw;
      // margin-right: 16vw;
      background-color: v.$primaryPinkDark;
      position: relative;
      top: -0.5rem;
      // top: 3.5rem;
      border-radius: 0 0 0 0;
      padding: 10% 2rem;
      text-align: center;
      @include m.tablet {
        padding: 6.5rem 2rem 2rem 2rem;
        // width: 85%;
        top: -1rem;
        // margin-left: 3rem;
        width: -webkit-fill-available;
        margin-left: 16vw;
        margin-right: 16vw;
        border-radius: 0 0 0 5rem;
        text-align: justify;
      }
      @include m.laptop {
        // width: 72.5%;
        margin-left: 19vw;
        margin-right: 19vw;
        // top: 4.5rem;
      }
      @include m.desktop {
        // width: 80%;
        // top: 6.5rem;
        margin-left: 16vw;
        margin-right: 16vw;
        border-radius: 0 0 0 10rem;
        padding: 12.5rem 4rem 4rem 4rem;
      }
      @include m.desktopXL {
        // width: 70%;
        // top: 6.5rem;
        // margin-left: 15%;
        margin-left: 20vw;
        margin-right: 20vw;
      }
      ul {
        li {
          font-family: v.$text;
          list-style: disc;
          list-style-position: inside;
          margin-left: 2rem;
        }
      }
      p {
        font-family: v.$text;
      }
    }
    .service-head-logo {
      display: none;
      position: absolute;
      right: -5%;
      top: -7.5rem;
      // padding: 2rem;
      background-color: v.$grayBackground;
      width: 15rem;
      height: 15rem;
      border-radius: 50% 50% 0 0;
      @include m.tablet {
        // padding-top: 1rem;
        display: flex;
        width: 12.5rem;
        height: 12.5rem;
        justify-content: center;
        align-items: center;
        // top: -5vw;
        // right: 11vw;
        // top: -2rem;
        // right: 3rem;
      }
      @include m.laptop {
        top: -10.5rem;
        width: 15.5rem;
        height: 15.5rem;
        // right: 10rem;
      }
      @include m.desktop {
        top: -17rem;
        width: 23rem;
        height: 23rem;
        // right: 10rem;
      }
      @include m.desktopXL {
        top: -19.5rem;
        right: -2.5%;
        width: 28rem;
        height: 28rem;
        // right: 25%;
        // padding: 1rem 2rem 5rem 2rem;
      }
      img {
        height: 60%;
        @include m.tablet {
          // padding: 0 0.5rem 1rem 0.5rem;
        }
        @include m.laptop {
          // padding: 0 1rem 2rem 1rem;
        }
        @include m.desktop {
          // padding: 4rem 4rem 1rem 4rem;
        }
      }
    }
  }
}

.service-gallery {
  background-color: v.$white;
  width: 80%;
  height: auto;
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  max-width: 120rem;
  margin: 2rem auto 2rem auto;
  @include m.mobile {
    grid-template-columns: repeat(1, 1fr);
    margin: 0 3rem 4rem 3rem;
  }
  @include m.tablet {
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 7.5rem;
    margin-left: auto;
    margin-right: auto;
  }
  @include m.laptop {
    margin-bottom: 10rem;
  }
  .service-img {
    width: 100%;
    border: 1rem solid v.$grayBackground;
    img {
      width: 100%;
    }
  }
}